import React from 'react';
import { Sidebar } from './../../Template/Sidebar';
import { AdminNavbar } from './../../Template/AdminNavbar';
import { Link } from 'react-router-dom'
import { CollapsibleBox12 } from './../../Template/CollapsibleBox12';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { TheTable } from './TheTable'
import { TheForm } from './TheForm'
import { Tooltip, Container, Row, Col, Card, CardBody, CardTitle, CardText, CardHeader,  Breadcrumb, BreadcrumbItem, Table, Form, FormGroup, Label, Input, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

export class TableForms extends React.Component{
	constructor(props){
		super(props);
		this.toggleModal = this.toggleModal.bind(this);
		this.state = {
			dataCar : [
				{
					id: Math.random(),
					car : "Bugati",
					price : 20000,
					country: "italia"
				},
				{
					id: Math.random(),
					car : "Maserati",
					price : 20000,
					country: "italia"
				},
				{
					id: Math.random(),
					car : "Ferari",
					price : 20000,
					country: "italia"
				},
				{
					id: Math.random(),
					car : "Koegnigseg",
					price : 20000,
					country: "italia"
				},
				{
					id: Math.random(),
					car : "Maybach",
					price : 120000,
					country: "italia"
				}
			],
			message: '',
			modal: false
		}
	}

	toggleModal(data){
		this.setState({
			modal: !this.state.modal
		})
	}

	addData = (newData) => {
		
		let dataCarForm = [...this.state.dataCar, newData]
		this.setState({
			dataCar: dataCarForm
		})
	}

	deleteData = (id) => {
		console.log(id);
		let dataCarTable = this.state.dataCar.filter(listCar => {
			return listCar.id != id
		})

		this.setState({
			dataCar: dataCarTable
		})
	}

	deleteAllData = (e) =>{
		let AlertDelete = window.confirm('Delete All Data ?');
		if(AlertDelete == true){
			this.setState({
				dataCar: [],
				message: 'No Data'
			})
		}else{
			this.setState({
				message: ''
			})
		}
	}
	render(){
		let num = 1;
		return(
			<section className='AdminMaster'>
				<Sidebar />
				<AdminNavbar />
				<div className='AdminMaster-Content'>
					<div className="TableForms-Admin-Master">
						<Container fluid>
							<Breadcrumb>
						        <BreadcrumbItem active className='BreadcrumbDashboard'>
						        	<Link to='/'> Dashboard </Link>
						        </BreadcrumbItem>
						        <BreadcrumbItem active className='BreadcrumbDashboard'>
						        	 Table Forms
						        </BreadcrumbItem>
						    </Breadcrumb>
							<Row>
								<CollapsibleBox12 title="Basic Form Input Table">
									<TheTable 
										dataCar={this.state.dataCar}
										deleteData={this.deleteData}
									/>
									{this.state.message.length > 0 ? (
											<Row>
												<Col lg='12' className='mx-auto'>
													<CardText style={{color: 'red'}}> {this.state.message} </CardText>
												</Col>
											</Row>
										) : 
										( null )
									}
								    <Modal isOpen={this.state.modal} toggle={this.toggleModal} className={this.props.className}>
							          <ModalHeader toggle={this.toggleModal}>Edit Data</ModalHeader>
							          <Form>
								          <ModalBody>
											<FormGroup row>
												<Label for="exampleEmail" sm={3}>Data</Label>
													<Col sm={9}>
													    <input type="teks" className='form-control'/>
													</Col>
											</FormGroup>
								          </ModalBody>
								          <ModalFooter>
									            <Button color="primary" onClick={this.toggleModal}>Update</Button>{' '}
									            <Button color="" onClick={this.toggleModal}>Cancel</Button>
								          </ModalFooter>
							          </Form>
							        </Modal>
								    <div className="FormInput">
								    	<Container>
								    		<Row>
								    			<Col lg='9' className='mx-auto'>
												    <TheForm 
												    	addData={this.addData}
												    	deleteAllData={this.deleteAllData}
												    />
											   	</Col>
											</Row>
										</Container>
								    </div>	
								</CollapsibleBox12>
							</Row>
						</Container>
					</div>
				</div>
				<div className='clearFix'> </div>
			</section>
		)
	}
}