import React from 'react'
import { Table, CardText, Row, Col } from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export const TheTable = (props) => {
		let number = 1;
		return(
			<Table responsive hover bordered size="sm">
				<thead>
						<tr>
							<th style={{width: "40px"}}>No</th>
							<th> Car </th>
							<th> Price </th>
							<th> Country </th>
							<th> Action </th>
						</tr>
				</thead>
				<tbody>
				{props.dataCar.map((listCar)=>{
						return(
							<tr key={listCar.id}>
								<td> {number++} </td>
								<td> {listCar.car} </td>
								<td> $.{listCar.price} </td>
								<td> {listCar.country} </td>
								<td>
								 <FontAwesomeIcon icon='trash-alt' onClick={() => {props.deleteData(listCar.id)}}/>
								</td>
							</tr>
							)
						})}   
				</tbody>
			</Table>
		)
}