import React from 'react'
import {Col, Form, FormGroup, Label, Input, Button} from 'reactstrap';
export class TheForm extends React.Component{
	state = {
		car: null,
		price: null,
		country: null
		
	}

	onChange = (e) => {
		this.setState({
			[e.target.id] : e.target.value
		})
	}

	onSubmit = (e) => {
		e.preventDefault();
		this.props.addData(this.state)
	}
	render(){
		return(
			<Form onSubmit={this.onSubmit}>
				<FormGroup row>
						<Label for="exampleEmail" sm={3}>Input Car</Label>
						<Col sm={9}>
							<input type="teks" className='form-control' id="car" onChange={this.onChange}/>
						</Col>
				</FormGroup>
				<FormGroup row>
						<Label for="exampleEmail" sm={3}>Price Data</Label>
						<Col sm={9}>
							<input type="teks" className='form-control' id="price" onChange={this.onChange}/>
						</Col>
				</FormGroup>
					<FormGroup row>
						<Label for="exampleEmail" sm={3}>Country Data</Label>
							<Col sm={9}>
								<input type="teks" className='form-control' id="country" onChange={this.onChange}/>
							</Col>
					</FormGroup>
					<FormGroup className='text-center'>   		
						<Button outline color="primary"> Add Data </Button>{' '}
						<Button outline color="danger" onClick={this.props.deleteAllData}> Delete All Data </Button>{' '}
				</FormGroup>
			</Form>
		)
	}
}